.button {
    padding: 8px 12px;
    font-size: 24px;
    color: #424242;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: all 0.3s;
}

.button:hover {
    color: #202020;
    background-color: #fff9bf;
    box-shadow: 0 0 6px #dfdfdf;
}

.button:disabled {
    pointer-events: none;
}
