.todo {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    gap: 15px;
    margin: 15px 0;
    font-size: 1.5rem;
    border-radius: 5px;
    border: 2px solid #666;
    color: #424242;
    background-color: #fbfef9;
}

.todoText {
    width: 100%;
    text-align: left;
}

.todoIcon {
    font-size: 1.8rem;
    color: rgb(0, 128, 117);
}

.completedTodo {
    color: #666;
    background-color: unset;
    border-color: #666;
}

.todo.completedTodo .todoIcon,
.todo.completedTodo .checkIcon,
.todo.completedTodo .deleteIcon {
    color: #666;
}

.checkIcon,
.deleteIcon {
    cursor: pointer;
    color: #d3d3d3;
    padding: 0 7px;
    font-size: 1.8rem;
    transition: all 0.3s;
}

.todo.completedTodo .deleteIcon:hover {
    color: #ff0000;
}
.checkIcon:hover,
.deleteIcon:hover {
    cursor: pointer;
    transform: scale(1.3);
}

.checkIcon:hover {
    color: #008000;
}

.deleteIcon:hover {
    color: #ff0000;
}
