.todoFormContainer {
    margin-bottom: 30px;
}

.todoFormContainer form {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.todoFormContainer input {
    width: 65%;
    padding: 8px 12px;
    font-size: 24px;
    color: #424242;
    border-radius: 5px;
    border: none;
    outline: none;
    transition: all 0.3s;
}
.todoFormContainer input::placeholder {
    font-size: 24px;
    color: #6e6e6e;
    transition: all 0.25s;
}
.todoFormContainer input:active::placeholder,
.todoFormContainer input:focus::placeholder {
    opacity: 0;
    transform: translateX(-10%);
}
.todoFormContainer input:focus {
    box-shadow: 0 0 6px #dfdfdf;
}
